.footer__policy a:active {
  color: #275699;
  border-color: #275699;
}

.social {
  display: flex;
  gap: 20px;
}
@media (max-width: 1199px) {
  .social {
    position: absolute;
    right: 0;
    bottom: 0;
    gap: 10px;
  }
}
@media (max-width: 767px) {
  .social {
    left: 0;
    justify-content: center;
  }
}

.social__link {
  width: 36px;
  height: 36px;
  background-color: #316dc2;
  display: flex;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  .social__link {
    width: 26px;
    height: 26px;
  }
  .social__link svg {
    width: 12px;
    height: 12px;
  }
}

.social__link:hover svg {
  opacity: 0.7;
}

.social__link:focus {
  outline: none;
}
.social__link:focus svg {
  opacity: 0.7;
}

.social__link:active {
  background-color: #275699;
}
.social__link:active svg {
  opacity: 1;
}